/* eslint-disable new-cap */
import http from '../../http-common'

class safesSercice {
  getAll () {
    return http.get('Safes')
  }

  getAllPagnation (params) {
    return http.get('Safes', { params })
  }

  get (id) {
    return http.get(`Safes/getById?Id=${id}`)
  }

  create (data) {
    return http.post('Safes', data)
  }

  update (id, data) {
    return http.put(`Safes?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Safes?Id=${id}`)
  }
}

export default new safesSercice()
